
const validateFormOne = (values) => {

    const isAlpha = (name) => {
        return /^[A-Za-zрЃљ-рЃ░]+$/.test(name)
    }

    const isRedberryEmail = (email) => {
        return /^[a-z0-9](\.?[a-z0-9]){2,}@redberry\.ge$/.test(email)
    }
    
    const isEmail = (email) =>{
        return /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)
    }
    
    let errors = {}

    if(values.name === '') {
        errors.name='рЃАрЃљрЃ«рЃћрЃџрЃўрЃА рЃЋрЃћрЃџрЃў рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ'
    }else if(!isAlpha(values.name)){
        errors.name='рЃАрЃљрЃ«рЃћрЃџрЃўрЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃўрЃфрЃљрЃЋрЃЊрЃћрЃА рЃЏрЃ«рЃЮрЃџрЃЮрЃЊ рЃљрЃџрЃцрЃљрЃЉрЃћрЃбрЃўрЃА рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃћрЃЉрЃА(рЃљ-рЃ░, a-z, A-Z)'
    }else if (values.name.length < 3) {
        errors.name='рЃАрЃљрЃ«рЃћрЃџрЃўрЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃЊрЃњрЃћрЃЉрЃЮрЃЊрЃћрЃА рЃЏрЃўрЃюрЃўрЃЏрЃБрЃЏ 3 рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃњрЃљрЃю'
    } else if(values.name.length > 255){
        errors.name='рЃАрЃљрЃ«рЃћрЃџрЃўрЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃЊрЃњрЃћрЃЉрЃЮрЃЊрЃћрЃА рЃЏрЃљрЃЦрЃАрЃўрЃЏрЃБрЃЏ 255 рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃАрЃњрЃљрЃю'
    }else{
        errors.name=''
    }

    if(values.lastName === '') {
        errors.lastName='рЃњрЃЋрЃљрЃарЃўрЃА рЃЋрЃћрЃџрЃў рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ'
    }else if(!isAlpha(values.lastName)){
        errors.lastName='рЃњрЃЋрЃљрЃарЃўрЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃўрЃфрЃљрЃЋрЃЊрЃћрЃА рЃЏрЃ«рЃЮрЃџрЃЮрЃЊ рЃљрЃџрЃцрЃљрЃЉрЃћрЃбрЃўрЃА рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃћрЃЉрЃА(рЃљ-рЃ░, a-z, A-Z)'
    }else if (values.lastName.length < 3) {
        errors.lastName='рЃњрЃЋрЃљрЃарЃўрЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃЊрЃњрЃћрЃЉрЃЮрЃЊрЃћрЃА рЃЏрЃўрЃюрЃўрЃЏрЃБрЃЏ 3 рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃњрЃљрЃю'
    } else if(values.lastName.length > 255){
        errors.lastName='рЃњрЃЋрЃљрЃарЃўрЃА рЃЋрЃћрЃџрЃў рЃБрЃюрЃЊрЃљ рЃерЃћрЃЊрЃњрЃћрЃЉрЃЮрЃЊрЃћрЃА рЃЏрЃљрЃЦрЃАрЃўрЃЏрЃБрЃЏ 255 рЃАрЃўрЃЏрЃЉрЃЮрЃџрЃЮрЃАрЃњрЃљрЃю'
    }else{
        errors.lastName=''
    }

    if(values.email === '') {
        errors.email='рЃЏрЃћрЃўрЃџрЃўрЃА рЃЋрЃћрЃџрЃў рЃАрЃљрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃБрЃџрЃЮрЃљ'
    } else if (!isEmail(values.email)) {
        errors.email='рЃЌрЃЦрЃЋрЃћрЃюрЃА рЃЏрЃўрЃћрЃа рЃерЃћрЃДрЃЋрЃљрЃюрЃўрЃџрЃў рЃЏрЃћрЃўрЃџрЃў рЃљрЃарЃљрЃАрЃгрЃЮрЃарЃўрЃљ'
    } else if(!isRedberryEmail(values.email)){
        errors.email='рЃњрЃЌрЃ«рЃЮрЃЋрЃЌ рЃЊрЃљрЃарЃћрЃњрЃўрЃАрЃбрЃарЃўрЃарЃЊрЃћрЃЌ рЃарЃћрЃЊрЃЉрЃћрЃарЃўрЃА рЃЏрЃћрЃўрЃџрЃўрЃЌ(youremail@redberry.ge)'
    }else{
        errors.email=''
    }

    return errors
}

export default validateFormOne