import React from "react";
import vector_r from '../images/vector-r.png'
import vector_e from '../images/vector-e.png'
import vector_d from '../images/vector-d.png'
import vector_b from '../images/vector-b.png'
import vector_y from '../images/vector-y.png'
import vector_cp from '../images/vector-cp.png'
import vector_next from '../images/vector-next.png'
import vector_prev from '../images/vector-prev.png'
import side_img_2 from '../images/side-img-2.png'

const FormTwo = (props) => {
    const {prevStep, handleChange, handleSubmit, values, errors } = props
    
    return(
        <div className='container'>
            <div className='inside'>
                <div className='top'>
                    <div className='redberry-container'>
                        <img className='redberry' src={vector_r} alt='r'/>
                        <img className='redberry' src={vector_e} alt='e'/>
                        <img className='redberry' src={vector_d} alt='d'/>
                        <img className='redberry' src={vector_b} alt='b'/>
                        <img className='redberry' src={vector_e} alt='e'/>
                        <img className='redberry' src={vector_r} alt='r'/>
                        <img className='redberry' src={vector_r} alt='r'/>
                        <img className='redberry' src={vector_y} alt='y'/>
                        <img className='cp' src={vector_cp} alt='cp'/>
                    </div>
                    <p className='step-count'>2/4</p>
                    <div className='top-line'></div>
                </div>
                <div className='mid'>
                    <div className='left'>
                        <form id='form-step2' className='radio-page-form' onSubmit={handleSubmit}>
                            <div className='radio-form '>
                                <p className='question'>рЃњрЃљрЃЦрЃЋрЃА рЃњрЃљрЃЊрЃљрЃбрЃљрЃюрЃўрЃџрЃў Covid-19?*</p>
                                <label className='radio-label'><input type='radio' name='hadCovid' checked={values.hadCovid==='yes'} value='yes' onChange={handleChange}/>  рЃЎрЃў</label>
                                <label className='radio-label'><input type='radio' name='hadCovid' checked={values.hadCovid==='no'} value='no' onChange={handleChange}/>  рЃљрЃарЃљ</label>
                                <label className='radio-label'><input type='radio' name='hadCovid' checked={values.hadCovid==='infected'} value='infected' onChange={handleChange}/>  рЃљрЃ«рЃџрЃљ рЃЏрЃљрЃЦрЃЋрЃА</label>
                            </div>
                            {values.hadCovid==='yes'&&
                                <div className='radio-form tested-for-antigens-form'>
                                    <p className='question'>рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА рЃбрЃћрЃАрЃбрЃў рЃњрЃљрЃЦрЃЋрЃА рЃњрЃљрЃЎрЃћрЃЌрЃћрЃЉрЃБрЃџрЃў?*</p>
                                    <label className='radio-label'><input type='radio' name='tested' checked={values.tested==='yes'} value='yes' onChange={handleChange}/>  рЃЎрЃў</label>
                                    <label className='radio-label'><input type='radio' name='tested' checked={values.tested==='no'} value='no' onChange={handleChange}/>  рЃљрЃарЃљ</label>
                                </div>
                            }
                            {values.tested==='yes' &&
                                <div className='form-control radio-form date-antigen-count'>
                                    <p className='question'>рЃЌрЃБ рЃњрЃљрЃ«рЃАрЃЮрЃЋрЃА, рЃњрЃЌрЃ«рЃЮрЃЋ рЃЏрЃўрЃБрЃЌрЃўрЃЌрЃћ рЃбрЃћрЃАрЃбрЃўрЃА рЃЏрЃўрЃљрЃ«рЃџрЃЮрЃћрЃЉрЃўрЃЌрЃў рЃарЃўрЃфрЃ«рЃЋрЃў рЃЊрЃљ рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА рЃарЃљрЃЮрЃЊрЃћрЃюрЃЮрЃЉрЃљ*</p>
                                    <input className='form-2-input' type='text' name='dateOfTest' value={values.dateOfTest} onChange={handleChange} placeholder='рЃарЃўрЃфрЃ«рЃЋрЃў'/>
                                    <small className='error-msg-form2'>{errors.dateOfTest}</small>
                                    <input className='form-2-input' type='text' name='antigenCount' value={values.antigenCount} onChange={handleChange} placeholder='рЃљрЃюрЃбрЃўрЃАрЃ«рЃћрЃБрЃџрЃћрЃЉрЃўрЃА рЃарЃљрЃЮрЃЊрЃћрЃюрЃЮрЃЉрЃљ'/>
                                    <small className='error-msg-form2'>{errors.antigenCount}</small>
                                </div>
                            }
                            {values.tested==='no' &&
                                <div className='form-control radio-form infection-date'>
                                    <p className='question'>рЃЏрЃўрЃБрЃЌрЃўрЃЌрЃћ рЃЏрЃўрЃљрЃ«рЃџрЃЮрЃћрЃЉрЃўрЃЌрЃў рЃърЃћрЃарЃўрЃЮрЃЊрЃў (рЃЊрЃдрЃћ/рЃЌрЃЋрЃћ/рЃгрЃћрЃџрЃў) рЃарЃЮрЃЊрЃўрЃА рЃњрЃЦрЃЮрЃюрЃЊрЃљ Covid-19*</p>
                                    <input className='form-2-input' type='text' name='dateInfected' value={values.dateInfected} onChange={handleChange} placeholder='рЃЊрЃЊ/рЃЌрЃЌ/рЃгрЃг'/>
                                    <small className='error-msg-form2'>{errors.dateInfected}</small>
                                </div>
                            }
                        </form>
                    </div>
                    <div className='right'>
                        <img className='side-img' src={side_img_2} alt='side-img'/>
                    </div>
                </div>
                <div className='bottom'>
                    <div className='nav-btn-container'>
                        <button className='navigation-btn' onClick={prevStep}><img id='prev' src={vector_prev} alt='prev'/></button>
                        <button className='navigation-btn' type='submit' form='form-step2'><img id='next' src={vector_next} alt='next'/></button>
                    </div>
                </div>
            </div>
                
        </div>
    )
}


export default FormTwo